module.exports = "#pro-plan {\n    color: #fff;\n    padding: 0 !important;\n}\n\n#pro-plan .cover {\n    height: 500px;\n    background-image: url('business.jpg');\n    background-size: auto 500px;\n    background-position: center;\n    border-radius: 6px 0 0 6px;\n}\n\n#pro-plan .form .form-container {\n    width: 100%;\n    padding: 30px 25px 0px 10px;\n}\n\n#pro-plan .form .form-container h4{\n    font-size: 18px;\n    line-height: 18px;\n    margin: 0 0 5px 0;\n}\n\n#pro-plan .form .form-container small{\n    font-size: 12px;\n    line-height: 12px;\n}\n\n#pro-plan .form .form-container aw-wizard{\n    margin: 15px 0 0 0;\n}\n\n#pro-plan .form .form-container aw-wizard-step{\n    margin: 20px 0 0 0;\n}\n\n/* ====================================================\nRecreating the email field from https://webflow.com/cms. Just an experiment - not as cross-browser friendly as the original.\nChanged:\n- animated gradient bar to :after element\n- flexbox for layout\n==================================================== */\n\ninput, textarea {\n    border-style: none;\n    background: transparent;\n    outline: none;\n  }\n\nbutton {\n    padding: 0;\n    background: none;\n    border: none;\n    outline: none;\n    color: #fff;\n  }\n\n@-webkit-keyframes gradient {\n    0% {\n      background-position: 0 0;\n    }\n    100% {\n      background-position: 100% 0;\n    }\n  }\n\n@keyframes gradient {\n    0% {\n      background-position: 0 0;\n    }\n    100% {\n      background-position: 100% 0;\n    }\n  }\n\n.webflow-style-input {\n    position: relative;\n    display: flex;\n    flex-direction: row;\n    width: 100%;\n    margin: 0 auto 10px auto;\n    border-radius: 2px;\n    padding: 8px 14px 10px;\n    background: rgba(57, 63, 84, 0.8);\n  }\n\n.webflow-style-input:after {\n    content: \"\";\n    position: absolute;\n    left: 0px;\n    right: 0px;\n    bottom: 0px;\n    z-index: 999;\n    height: 2px;\n    border-bottom-left-radius: 2px;\n    border-bottom-right-radius: 2px;\n    background-position: 0% 0%;\n    background: linear-gradient(to right, #B294FF, #57E6E6, #FEFFB8, #57E6E6, #B294FF, #57E6E6);\n    background-size: 500% auto;\n    -webkit-animation: gradient 3s linear infinite;\n            animation: gradient 3s linear infinite;\n  }\n\n.webflow-style-input input {\n    flex-grow: 1;\n    color: #BFD2FF;\n    font-size: 14px;\n    vertical-align: middle;\n  }\n\n.webflow-style-input input::-webkit-input-placeholder {\n    color: #7881A1;\n  }\n\n.webflow-style-input.invalid {\n    background-color: rgba(84, 57, 57, 0.8) !important;\n  }\n\n.webflow-style-input textarea {\n    color: #BFD2FF;\n    font-size: 14px;\n    vertical-align: middle;\n    width: 100%;\n    resize: none;\n  }\n\n.webflow-style-input textarea::-webkit-input-placeholder {\n    color: #7881A1;\n  }\n\n.webflow-style-input textarea::-moz-placeholder {\n    color: #7881A1;\n  }\n\n.webflow-style-input textarea::-ms-input-placeholder {\n    color: #7881A1;\n  }\n\n.webflow-style-input textarea::placeholder {\n    color: #7881A1;\n  }\n\n.webflow-style-button {\n    position: relative;\n    flex-direction: row;\n    width: 30%;\n    min-width: 160px;\n    display: inline-table;\n    border-radius: 2px;\n    padding: 10px 14px 12px;\n    background: rgba(57, 63, 84, 0.8);\n  }\n\n.webflow-style-button.right {\n    float: right;\n  }\n\n.webflow-style-button.right.invalid {\n    background-color: rgba(84, 57, 57, 0.8) !important;\n  }\n\n.webflow-style-button:after {\n    content: \"\";\n    position: absolute;\n    left: 0px;\n    right: 0px;\n    bottom: 0px;\n    z-index: 999;\n    height: 2px;\n    border-bottom-left-radius: 2px;\n    border-bottom-right-radius: 2px;\n    background-position: 0% 0%;\n    background: linear-gradient(to right, #B294FF, #57E6E6, #FEFFB8, #57E6E6, #B294FF, #57E6E6);\n    background-size: 500% auto;\n    -webkit-animation: gradient 3s linear infinite;\n            animation: gradient 3s linear infinite;\n  }\n\n.webflow-style-button button {\n    color: #BFD2FF;\n    font-size: 18px;\n    vertical-align: middle;\n    transition: color .25s;\n    width: 100%;\n    text-align: center;\n    text-transform: uppercase;\n  }\n\n.webflow-style-button button:hover {\n    color: #BFD2FF;\n  }\n\n/*\nP.S: if you like my content maybe you will become a donator and donate some money? That helps me to create new awesome materials. https://www.paypal.me/melnik909\n*/\n\n/*\nI've used nested span elements for creating an animation of square turn and creating an arrow animation. But if you know other a solution please email me\nmelnik909@ya.ru\n*/\n\n/*\n=====\nLEVEL 1. CORE STYLES\n=====\n*/\n\n.term {\n    padding-top: 5px;\n    text-transform: uppercase;\n}\n\n.toggle{\n    --uiToggleSize: var(--toggleSize, 20px);\n    --uiToggleIndent: var(--toggleIndent, .4em);\n    --uiToggleBorderWidth: var(--toggleBorderWidth, 2px);\n    --uiToggleColor: var(--toggleColor, #000);\n    --uiToggleDisabledColor: var(--toggleDisabledColor, #868e96);\n    --uiToggleBgColor: var(--toggleBgColor, #fff);\n    --uiToggleArrowWidth: var(--toggleArrowWidth, 2px);\n    --uiToggleArrowColor: var(--toggleArrowColor, #fff);\n  \n    display: inline-block;\n    position: relative;\n  }\n\n.toggle__input{\n    position: absolute;\n    left: -99999px;\n  }\n\n.toggle__label{\n    display: inline-flex;\n    cursor: pointer;\n    min-height: var(--uiToggleSize);\n    padding-left: calc(var(--uiToggleSize) + var(--uiToggleIndent) + 5px);\n  }\n\n.toggle__label:before, .toggle__label:after{\n    content: \"\";\n    box-sizing: border-box;  \n    width: 1em;\n    height: 1em;\n    font-size: var(--uiToggleSize);\n  \n    position: absolute;\n    left: 0;\n    top: 0;\n  }\n\n.toggle__label:before{\n    border: var(--uiToggleBorderWidth) solid var(--uiToggleColor);\n    z-index: 2;\n  }\n\n.toggle__input:disabled ~ .toggle__label:before{\n    border-color: var(--uiToggleDisabledColor);\n  }\n\n.toggle__input:focus ~ .toggle__label:before{\n    box-shadow: 0 0 0 2px var(--uiToggleBgColor), 0 0 0px 4px var(--uiToggleColor);\n  }\n\n.toggle__input:not(:disabled):checked:focus ~ .toggle__label:after{\n    box-shadow: 0 0 0 2px var(--uiToggleBgColor), 0 0 0px 4px var(--uiToggleColor);\n  }\n\n.toggle__input:not(:disabled) ~ .toggle__label:after{\n    background-color: var(--uiToggleColor);\n    opacity: 0;\n  }\n\n.toggle__input:not(:disabled):checked ~ .toggle__label:after{\n    opacity: 1;\n  }\n\n.toggle__text{\n    margin-top: auto;\n    margin-bottom: auto;\n  }\n\n/*\n  The arrow size and position depends from sizes of square because I needed an arrow correct positioning from the top left corner of the element toggle\n  */\n\n.toggle__text:before{\n    content: \"\";\n    box-sizing: border-box;\n    width: 0;\n    height: 0;\n    font-size: var(--uiToggleSize);\n  \n    border-left-width: 0;\n    border-bottom-width: 0;\n    border-left-style: solid;\n    border-bottom-style: solid;\n    border-color: var(--uiToggleArrowColor);\n  \n    position: absolute;\n    top: .5428em;\n    left: .2em;\n    z-index: 3;\n  \n    -webkit-transform-origin: left top;\n  \n            transform-origin: left top;\n    -webkit-transform: rotate(-40deg) skew(10deg);\n            transform: rotate(-40deg) skew(10deg);\n  }\n\n.toggle__input:not(:disabled):checked ~ .toggle__label .toggle__text:before{\n    width: .5em;\n    height: .25em;\n    border-left-width: var(--uiToggleArrowWidth);\n    border-bottom-width: var(--uiToggleArrowWidth);\n    will-change: width, height;\n    transition: width .1s ease-out .2s, height .2s ease-out;\n  }\n\n/*\n  =====\n  LEVEL 2. PRESENTATION STYLES\n  =====\n  */\n\n/* \n  The demo skin\n  */\n\n.toggle__label:before, .toggle__label:after{\n    border-radius: 2px;\n  }\n\n/* \n  The animation of switching states\n  */\n\n.toggle__input:not(:disabled) ~ .toggle__label:before,\n  .toggle__input:not(:disabled) ~ .toggle__label:after{\n    opacity: 1;\n    -webkit-transform-origin: center center;\n            transform-origin: center center;\n    will-change: transform;\n    transition: -webkit-transform .2s ease-out;\n    transition: transform .2s ease-out;\n    transition: transform .2s ease-out, -webkit-transform .2s ease-out;\n  }\n\n.toggle__input:not(:disabled) ~ .toggle__label:before{\n    -webkit-transform: rotateY(0deg);\n            transform: rotateY(0deg);\n    transition-delay: .2s;\n  }\n\n.toggle__input:not(:disabled) ~ .toggle__label:after{\n    -webkit-transform: rotateY(90deg);\n            transform: rotateY(90deg);\n  }\n\n.toggle__input:not(:disabled):checked ~ .toggle__label:before{\n    -webkit-transform: rotateY(-90deg);\n            transform: rotateY(-90deg);\n    transition-delay: 0s;\n  }\n\n.toggle__input:not(:disabled):checked ~ .toggle__label:after{\n    -webkit-transform: rotateY(0deg);\n            transform: rotateY(0deg);\n    transition-delay: .2s;\n  }\n\n.toggle__text:before{\n    opacity: 0;\n  }\n\n.toggle__input:not(:disabled):checked ~ .toggle__label .toggle__text:before{\n    opacity: 1;\n    transition: opacity .1s ease-out .3s, width .1s ease-out .5s, height .2s ease-out .3s;\n  }\n\n/*\n  =====\n  LEVEL 3. SETTINGS\n  =====\n  */\n\n.toggle{\n    --toggleColor: #444a5f;\n    --toggleBgColor: #444a5f88;\n    --toggleSize: 25px;\n  }\n\n.payment-button{\n    margin: 60px 0 !important;\n    width: 100%;\n  }\n\n.payment-button .pagseguro {\n    margin: 60px 0 !important;\n    width: 100%;\n  }\n\n.payment-button img{\n    width: 300px;\n    max-width: 100%;\n    margin: auto;\n  }\n/*# sourceMappingURL=data:application/json;base64, */"